.location{
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: column;
      flex-direction: column;
  width: 100%;
  height: 100vh;
  overflow: hidden;
  background-color: rgba(31, 14, 0, 0);
}

.div1{
  overflow: hidden;
  width: 100%;
  height: 100%;
}

.div2{
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: distribute;
      justify-content: space-around;

  -ms-flex-direction: column;

      flex-direction: column;
  width: 100%;
  height: 100%;
  font-size: 3.5vmin;
}

.abouttext{
  padding: 2vmin;
}


@media all and (orientation: landscape){
  .location{
    -ms-flex-direction: row;
        flex-direction: row;
  }
}


@-webkit-keyframes flowbg {
  0%{ background-color: rgb(19, 0, 19);}
  50%{ background-color: rgb(16, 0, 26);}
  100%{ background-color: rgb(19, 0, 19);}
}


@keyframes flowbg {
  0%{ background-color: rgb(19, 0, 19);}
  50%{ background-color: rgb(16, 0, 26);}
  100%{ background-color: rgb(19, 0, 19);}
}


