.main {
  display: -ms-flexbox;
  display: flex;
  width: 100%;
  height: 100vh;
  background: -webkit-gradient(linear, left top, right top, from(red), color-stop(violet), color-stop(indigo),color-stop(red), color-stop(orange),color-stop(red),color-stop(indigo),color-stop(violet),to(red));
  background: -webkit-linear-gradient(left, red, violet, indigo,red, orange,red,indigo,violet,red);
  background: -o-linear-gradient(left, red, violet, indigo,red, orange,red,indigo,violet,red);
  background: linear-gradient(to right, red, violet, indigo,red, orange,red,indigo,violet,red);
  overflow: hidden;
}

.herobg {
  position: absolute;
  width: 100%;
  height: 99vh;
  background-color: rgb(10, 0, 10);
  z-index: 0;
}

.shock1 {
  position: absolute;
  width: 100%;
  height: 99vh;
  z-index: 1;
  -webkit-animation: flashShock infinite 6s linear;
          animation: flashShock infinite 6s linear;
}

.shock2 {
  position: absolute;
  width: 100%;
  height: 99vh;
  z-index: 2;
  -webkit-animation: flashShock infinite 5s linear;
          animation: flashShock infinite 5s linear;
}

.shock3 {
  position: absolute;
  width: 100%;
  height: 99vh;
  z-index: 3;
  -webkit-animation: flashShock infinite 4s linear;
          animation: flashShock infinite 4s linear;
}

.layer1 {
  width: 100%;
  height: 99vh;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: center;
      justify-content: center;
  -ms-flex-align: center;
      align-items: center;
  overflow: hidden;
  z-index: 4;
}

.layer2 {
  position: absolute;
  overflow: hidden;
  width: 100%;
  height: 99vh;
  z-index: 5;
}

.layer3 {
  position: absolute;
  overflow: hidden;
  display: -ms-flexbox;
  display: flex;
  width: 100%;
  height: 99vh;
  z-index: 6;
}

.layer4 {
  width: 100%;
  height: 100vh;
  position: absolute;
  z-index: 7;
}

.talogo {
  width: 25vmax;
  height: 25vmax;
  margin: auto auto;
  fill: rgb(105, 105, 105);
  -webkit-animation: pulseColor infinite 5s linear;
          animation: pulseColor infinite 5s linear;
}

.taWebs {
  -ms-flex: none;
      flex: none;
  width: 100vmax;
  height: 100vmax;
  fill: rgb(28, 0, 41);
  stroke:#530043;
  stroke-width:1;
  -webkit-animation: pulseColor2 infinite 5s linear;
          animation: pulseColor2 infinite 5s linear;
}

.taLightning {
  width: 100%;
  height: 100%;
  fill: none;
  stroke:#ffffff;
  stroke-width: 5;
  -webkit-animation: lightningRotate infinite 120s linear;
          animation: lightningRotate infinite 120s linear;
}

.arrow {
  margin-top: 80vh;
  margin-left: 50vw;
  height: 10vmin;
  stroke: none;
  fill: #530043;
  -webkit-animation: moveArrow infinite 2s linear;
          animation: moveArrow infinite 2s linear;
}

.flash {
  -webkit-animation: lightningFlash infinite 6s linear;
          animation: lightningFlash infinite 6s linear;
}

.flash2 {
  -webkit-animation: lightningFlash infinite 5s linear;
          animation: lightningFlash infinite 5s linear;
}

.flash3 {
  -webkit-animation: lightningFlash infinite 4s linear;
          animation: lightningFlash infinite 4s linear;
}

@-webkit-keyframes pulseColor {
  0%{ fill: rgb(255, 153, 0);}
  25%{ fill:rgb(255, 0, 0);}
  50%{ fill:rgb(183, 0, 255);}
  100%{ fill: rgb(255, 153, 0);}
}

@keyframes pulseColor {
  0%{ fill: rgb(255, 153, 0);}
  25%{ fill:rgb(255, 0, 0);}
  50%{ fill:rgb(183, 0, 255);}
  100%{ fill: rgb(255, 153, 0);}
}

@-webkit-keyframes pulseColor2 {
  0%{ fill: rgb(12, 0, 17);}
  50%{ fill:rgb(38, 0, 48);}
  100%{ fill: rgb(12, 0, 17);}
}

@keyframes pulseColor2 {
  0%{ fill: rgb(12, 0, 17);}
  50%{ fill:rgb(38, 0, 48);}
  100%{ fill: rgb(12, 0, 17);}
}

@-webkit-keyframes lightningRotate {
  0%{-webkit-transform: rotate(0deg);transform: rotate(0deg);}
  100%{-webkit-transform: rotate(360deg);transform: rotate(360deg);}
}

@keyframes lightningRotate {
  0%{-webkit-transform: rotate(0deg);transform: rotate(0deg);}
  100%{-webkit-transform: rotate(360deg);transform: rotate(360deg);}
}


@-webkit-keyframes lightningFlash {
  0%{ stroke: rgba(255, 255, 255, 1);}
  5%{ stroke: rgb(255, 255, 255, 1);}
  11%{ stroke:rgba(255, 255, 255, 0);}
  100%{ stroke:rgba(255, 255, 255, 0);}
}


@keyframes lightningFlash {
  0%{ stroke: rgba(255, 255, 255, 1);}
  5%{ stroke: rgb(255, 255, 255, 1);}
  11%{ stroke:rgba(255, 255, 255, 0);}
  100%{ stroke:rgba(255, 255, 255, 0);}
}

@-webkit-keyframes flashShock {
  0%{ background-color: rgba(0, 0, 0, 0); }
  3%{ background-color: rgba(255, 255, 255, 0.37); }
  7%{ background-color: rgba(255, 255, 255, 0.37); }
  14%{ background-color: rgba(0, 0, 0, 0); }
  100%{ background-color: rgba(0, 0, 0, 0); }
}

@keyframes flashShock {
  0%{ background-color: rgba(0, 0, 0, 0); }
  3%{ background-color: rgba(255, 255, 255, 0.37); }
  7%{ background-color: rgba(255, 255, 255, 0.37); }
  14%{ background-color: rgba(0, 0, 0, 0); }
  100%{ background-color: rgba(0, 0, 0, 0); }
}

@-webkit-keyframes moveArrow {
  0%{-webkit-transform: translate(-5vmin, 0);transform: translate(-5vmin, 0); fill:red;}
  90%{-webkit-transform: translate(-5vmin, 5vh);transform: translate(-5vmin, 5vh); fill:orange;}
  100%{-webkit-transform: translate(-5vmin, 0);transform: translate(-5vmin, 0); fill:red;}
}

@keyframes moveArrow {
  0%{-webkit-transform: translate(-5vmin, 0);transform: translate(-5vmin, 0); fill:red;}
  90%{-webkit-transform: translate(-5vmin, 5vh);transform: translate(-5vmin, 5vh); fill:orange;}
  100%{-webkit-transform: translate(-5vmin, 0);transform: translate(-5vmin, 0); fill:red;}
}


