.navBar{
  position: fixed;
  width: 100%;
  height: 5vmin;
  background-color: rgba(0, 0, 0, 0.25);
  background: -webkit-gradient(linear, left top, right top, from(rgb(0, 0, 0)), to(rgba(0, 0, 0, 0.5)));
  background: -webkit-linear-gradient(left, rgb(0, 0, 0), rgba(0, 0, 0, 0.5));
  background: -o-linear-gradient(left, rgb(0, 0, 0), rgba(0, 0, 0, 0.5));
  background: linear-gradient(to right, rgb(0, 0, 0), rgba(0, 0, 0, 0.5));
  display: -ms-flexbox;
  display: flex;
  z-index: 10;
}

.theNavSelection{
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: row;
      flex-direction: row;

}


.homebox{
  font-size: 3.5vmin;
  margin-left: 5vmin;
  text-align: center;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: column;
      flex-direction: column;
}

.homebox:hover{
  cursor: pointer;
  color: rgb(247, 0, 255);
}

.selected{
  margin: auto;
  background-color: rgb(161, 161, 161);
  width: 9vmin;
  height: 0.2vmin;
  -webkit-animation: homeanimate 1 0.5s linear;
          animation: homeanimate 1 0.5s linear;
}

.selected2{
  margin: auto;
  background-color: rgb(161, 161, 161);
  width: 9vmin;
  height: 0.2vmin;
  -webkit-animation: aboutanimate 1 0.5s linear;
          animation: aboutanimate 1 0.5s linear;
}

.selected3{
  margin: auto;
  background-color: rgb(161, 161, 161);
  width: 13vmin;
  height: 0.2vmin;
  -webkit-animation: servicesanimate 1 0.5s linear;
          animation: servicesanimate 1 0.5s linear;
}

.selected4{
  margin: auto;
  background-color: rgb(161, 161, 161);
  width: 12vmin;
  height: 0.2vmin;
  -webkit-animation: contactanimate 1 0.5s linear;
          animation: contactanimate 1 0.5s linear;
}

.homebox:hover .homebar{
  margin: auto;
  background-color: rgb(247, 0, 255);
  width: 9vmin;
  height: 0.2vmin;
  -webkit-animation: homeanimate 1 0.5s linear;
          animation: homeanimate 1 0.5s linear;
}

.homebox:hover .aboutbar{
  margin: auto;
  background-color: rgb(247, 0, 255);
  width: 9vmin;
  height: 0.2vmin;
  -webkit-animation: aboutanimate 1 0.5s linear;
          animation: aboutanimate 1 0.5s linear;
}

.homebox:hover .servicesbar{
  margin: auto;
  background-color: rgb(247, 0, 255);
  width: 12vmin;
  height: 0.2vmin;
  -webkit-animation: servicesanimate 1 0.5s linear;
          animation: servicesanimate 1 0.5s linear;
}

.homebox:hover .contactbar{
  margin: auto;
  background-color: rgb(247, 0, 255);
  width: 12vmin;
  height: 0.2vmin;
  -webkit-animation: contactanimate 1 0.5s linear;
          animation: contactanimate 1 0.5s linear;
}

.homebox:hover .blogbar{
  margin: auto;
  background-color: rgb(247, 0, 255);
  width: 6.5vmin;
  height: 0.2vmin;
  -webkit-animation: bloganimate 1 0.5s linear;
          animation: bloganimate 1 0.5s linear;
}

@-webkit-keyframes homeanimate {
  0%{width: 0vmin;}
  100%{width: 9vmin;}
}

@keyframes homeanimate {
  0%{width: 0vmin;}
  100%{width: 9vmin;}
}

@-webkit-keyframes aboutanimate {
  0%{width: 0vmin;}
  100%{width: 9vmin;}
}

@keyframes aboutanimate {
  0%{width: 0vmin;}
  100%{width: 9vmin;}
}

@-webkit-keyframes servicesanimate {
  0%{width: 0vmin;}
  100%{width: 12vmin;}
}

@keyframes servicesanimate {
  0%{width: 0vmin;}
  100%{width: 12vmin;}
}

@-webkit-keyframes contactanimate {
  0%{width: 0vmin;}
  100%{width: 12vmin;}
}

@keyframes contactanimate {
  0%{width: 0vmin;}
  100%{width: 12vmin;}
}

@-webkit-keyframes bloganimate {
  0%{width: 0vmin;}
  100%{width: 6.5vmin;}
}

@keyframes bloganimate {
  0%{width: 0vmin;}
  100%{width: 6.5vmin;}
}


